.slide-courses {
  border-radius: var(--border-10);

  &--1 {
    background-color: #def1ff;
  }

  &--2 {
    background-color: #fdede4;
  }

  &--3 {
    background-color: #efefff;
    ;
  }

  &__content {
    margin: 40px;
  }

  &__lang {
    width: 50px;
    height: 50px;
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 26px;
    color: var(--title-color);
  }

  &__duration {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 16px;
    color: var(--grey-color58);
  }

  &__module {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    color: var(--grey-color58);
  }

  &__value {
    font-weight: 700;
    color: var(--grey-color);
  }

  &__control {
    @include flex-all-sb;
  }

  &__link {
    @include flex-v-center;
  }

  &__word {
    margin-right: 10px;
  }

  &__price {
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    color: var(--grey-color);
  }
}
