.forwhom-list {

  &__item {
    padding: 40px;
    grid-column: 6 span;
    background-position: left center;
    background-repeat: no-repeat;

    &:nth-child(-n+2) {
      padding-top: 0;
    }

    &:nth-last-child(-n+2) {
      padding-bottom: 0;
    }

    &--1 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #E4D3E8;
        background-size: 36px;
        background-position: top 55% left 50%;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/schoolboy.png");
      }
    }

    &--2 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #D5E9F6;
        background-size: 40px;
        background-position: top 45% left 56%;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/student.png");
      }
    }

    &--3 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #FFF1CC;
        background-size: 36px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/emigrant.png");
      }
    }

    &--4 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #FAD3D3;
        background-size: 36px;
        background-position: top 57% left 50%;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/communicative.png");
      }
    }

    &--5 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #D5E9F6;
        background-size: 36px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/business.png");
      }
    }

    &--6 {
      &::before {
        content: "";
        display: inline-block;
        margin-bottom: 14px;
        width: 60px;
        height: 60px;
        border-radius: 50px;
        background-color: #E4D3E8;
        background-size: 36px;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url("../img/forwhom/travel.png");
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-size: 24px;
    font-weight: 700;
    color: var(--title-color);
  }

  &__descr {
    margin: 0;
    font-weight: 500;
    color: var(--grey-color58);
  }
}
