.tech {
  margin-bottom: 100px;

  &__inner {
    align-items: center;
    padding: 90px;
    border-radius: 40px;
    background: linear-gradient(135deg, rgba(46, 144, 209, 0.1) 0%, rgba(120, 37, 141, 0.1) 100%);
  }

  &__img {
    grid-column: 5 span;
  }

  &__content {
    grid-column: 7 / 6 span;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;

    &--gradient {
      color: transparent;
      background-clip: text;
      background-image: linear-gradient(172deg, #2E90D1 0%, #78258D 100%);
    }
  }

  // &__list {}

  &__item {

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__descr {
    margin: 0;
    padding-left: 36px;
    font-weight: 500;
    font-size: 20px;
    color: var(--grey-color58);
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 4px;
      left: 0;
      width: 16px;
      border-radius: 100%;
      height: 16px;
      background: linear-gradient(135deg, #2E90D1 0%, #78258D 100%);
    }
  }
}
