.work-list {
  counter-reset: work;

  &__item {
    grid-column: 4 span;
    box-shadow: 0px 4px 20px #F7F6FB;
    border-radius: 20px;
    position: relative;
  }

  &__content {
    padding: 120px 40px 40px 40px;
    &--1 {
      &::before {
        content: '';
        position: absolute;
        top: 40px;
        left: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: #2e90d1;
        border-radius: 100px;
        counter-increment: work;
        content: '0' counter(work);
      }
    }

    &--2 {
      &::before {
        content: '';
        position: absolute;
        top: 40px;
        left: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: var(--accent-color);
        border-radius: 100px;
        counter-increment: work;
        content: '0' counter(work);
      }
    }

    &--3 {
      &::before {
        content: '';
        position: absolute;
        top: 40px;
        left: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: #e42125;
        border-radius: 100px;
        counter-increment: work;
        content: '0' counter(work);
      }
    }

  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    font-size: 24px;
  }

  &__descr {
    margin: 0;
    margin-bottom: 35px;
    font-weight: 500;
    color: var(--grey-color58);
  }
}
