.hero {
  padding: 100px 0;

  &-container {
    display: flex;
    align-items: center;
  }

  &__content {
    align-items: center;
  }

  &__text {
    grid-column: 6 span;
  }

  &__overhead {
    margin: 0;
    margin-bottom: 15px;
    color: var(--blue-color);
    font-weight: 600;
    font-size: 18px;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 900;
    font-size: 60px;
    max-width: 698px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 56px;
    font-weight: 500;
    font-size: 20px;
    color: var(--grey-color82);
    max-width: 491px;
  }

  &__img {
    grid-column: 6 span;
    position: relative;

    &:before {
      content: "";
      display: inline-block;
      position: absolute;
      top: 137px;
      left: 134px;
      width: 107px;
      height: 80px;
      background-image: url("../img/hero-animation.png");
      background-size: cover;
      background-repeat: no-repeat;
      animation-duration: 2.1s;
      animation-iteration-count: infinite;
      animation-name: hero-animation;
      animation-timing-function: cubic-bezier(0.280, 0.840, 0.420, 1);
    }
  }

  @keyframes hero-animation {
    0% {
      transform: scale(1, 1) translateY(0);
    }

    10% {
      transform: scale(1.1, .9) translateY(0);
    }

    30% {
      transform: scale(.9, 1.1) translateY(-90px);
    }

    50% {
      transform: scale(1.05, .95) translateY(0);
    }

    57% {
      transform: scale(1, 1) translateY(-8px);
    }

    64% {
      transform: scale(1, 1) translateY(0);
    }

    100% {
      transform: scale(1, 1) translateY(0);
    }
  }
}
