.benefits-list {
  counter-reset: benefits;

  &__item {
    align-items: center;
    grid-auto-flow: dense;

    &:not(:last-child) {
      margin-bottom: 60px;
    }
  }

  &__content {
    grid-column: 6 span;
    position: relative;
    padding-top: 68px;

    &--reverse {
      grid-column: 7 / 6 span;
      padding-left: 135px;
    }

    &--1 {
      &::before {
        content: '';
        position: absolute;
        top: 23px;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: #2e90d1;
        border-radius: 100px;
        counter-increment: benefits;
        content: '0' counter(benefits);
      }
    }

    &--2 {
      &::before {
        content: '';
        position: absolute;
        top: 23px;
        left: 135px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: var(--accent-color);
        border-radius: 100px;
        counter-increment: benefits;
        content: '0' counter(benefits);
      }
    }

    &--3 {
      &::before {
        content: '';
        position: absolute;
        top: 23px;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 63px;
        height: 28px;
        font-weight: 700;
        color: var(--light-color);
        background-color: #e42125;
        border-radius: 100px;
        counter-increment: benefits;
        content: '0' counter(benefits);
      }
    }
  }

  &__img {
    grid-column: 7 / 6 span;

    &--reverse {
      grid-column: 6 span;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    color: var(--title-color);
    font-weight: 700;
    line-height: 113%;
    font-size: 36px;
  }

  &__descr {
    margin: 0;
    color: var(--grey-color58);
    font-weight: 500;
    font-size: 20px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &--1 {
      max-width: 512px;
    }

    &--2 {
      max-width: 507px;
    }

    &--3 {
      max-width: 578px;
    }
  }
}
