// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Gilroy", sans-serif;
  --content-width: 1440px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --accent-color: #78258d;
  --title-color: #232323;
  --grey-color: #333;
  --grey-color82: #828282;
  --grey-color58: #585858;
  --blue-color: #2e90d1;

  // border
  --border-8: 8px;
  --border-10: 10px;
}
