.forwhom {
  margin-bottom: 140px;

  &__main {
    grid-column: 4 span;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 5%;
      left: -32%;
      display: inline-block;
      width: 140%;
      height: 100%;
      transform: skew(-12deg, 12deg);
      background-image: url("../img/forwhom/message.png");
      background-position: top left;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &__title {
    margin: 0;
    max-width: 400px;
    margin-bottom: 27px;

    &-blue {
      color: var(--blue-color);
    }
  }

  &__suptitle {
    margin: 0;
    margin-bottom: 40px;
    color: var(--grey-color58);
    font-weight: 500;

    &-blue {
      color: var(--blue-color);
    }
  }

  &__elements {
    grid-column: 8 span;
  }
}
